import { html, css, LitElement } from "lit";

export class TodoList extends LitElement {
  static styles = css`
    p {
      color: blue;
    }
  `;

  static properties = {};

  constructor() {
    super();
  }

  render() {
    return html`<label>
      <input type="checkbox" checked=false />
    </label> `;
  }
}
customElements.define("todo-list", TodoList);
